import {
  View,
  SafeAreaView,
  Image,
  Text,
  StyleSheet,
  TouchableHighlight,
} from 'react-native';
import { switchIsLoaded } from './gamePage';
import colors from './colors';

export function HomePage({ navigation }) {
  return (
    <SafeAreaView style={homeStyles.screenContainer}>
    <View style={homeStyles.instructionsContainer}>
      <View style={{ flex: 1 }}></View>

      <View style={homeStyles.logoContainer}>
        <Image
          style={homeStyles.logo}
          source={require('../../assets/logoDone.png')}
        />
      </View>

      <View style={homeStyles.buttonsContainer}>
        <View style={homeStyles.playContainer}>
          <TouchableHighlight
            underlayColor="green"
            style={homeStyles.playButton}
            onPress={() => {
              navigation.push('GamePage');
              switchIsLoaded();
            }}>
            <Text style={homeStyles.playButtonText}> Play </Text>
          </TouchableHighlight>
        </View>
        <View style={homeStyles.optionsContainer}>
          <TouchableHighlight
            underlayColor="green"
            style={homeStyles.optionButtons}
            onPress={() => {
              navigation.push('StatsPage');
            }}>
            <Text style={homeStyles.optionButtonText}> Options </Text>
          </TouchableHighlight>
          <TouchableHighlight
            underlayColor="green"
            style={homeStyles.optionButtons}
            onPress={() => {
              navigation.push('TutorialPage');
            }}>
            <Text style={homeStyles.optionButtonText}> Instructions </Text>
          </TouchableHighlight>
        </View>
      </View>
    </View>
    </SafeAreaView>
  );
}
const homeStyles = StyleSheet.create({
  screenContainer: {
    backgroundColor: 'white',
    width: '100%',
    height: '100%',
    alignItems: 'center',
  },
  instructionsContainer: {
    backgroundColor: '#FDF2D6',
    width: 500,
    height: 1000,
    borderWidth: 1,
    borderColor: 'black',
  },
  playButton: {
    width: '75%',
    height: 75,
    margin: 10,
    borderRadius: 45,
    backgroundColor: '#74C29C',
    justifyContent: 'center',
    alignItems: 'center',
    shadowColor: '#000',
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 4,
    borderColor: '#51876d',
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderBottomWidth: 5,
    shadowOffset: { width: -4, height: 10 },
  },
  optionButtons: {
    width: '35%',
    height: 75,
    margin: 10,
    borderRadius: 45,
    backgroundColor: '#74C29C',
    justifyContent: 'center',
    alignItems: 'center',
    shadowColor: '#000',
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 4,
    borderColor: '#51876d',
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderBottomWidth: 5,
    shadowOffset: { width: -4, height: 10 },
  },
  playButtonText: {
    color: colors.buttonTextColor,
    fontWeight: 'bold',
    fontSize: 30,
  },
  optionButtonText: {
    color: colors.buttonTextColor,
    fontWeight: 'bold',
    fontSize: 18,
  },
  logoContainer: {
    flex: 3,
    justifyContent: 'center',
    alignItems: 'center',
  },
  buttonsContainer: {
    flex: 2,
    justifyContent: 'center',
    alignItems: 'center',
  },
  logo: {
    width: '100%',
    height: '100%',
  },
  playContainer: {
    flex: 1,
    width: '100%',
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  optionsContainer: {
    flexDirection: 'row',
    flex: 1,
    width: '100%',
    justifyContent: 'center',
  },
});
