import { useRef, useEffect } from 'react';
import {
  View,
  Text,
  Image,
  Animated,
  StyleSheet,
  Easing,
  Dimensions,
  TouchableHighlight,
  SafeAreaView,
} from 'react-native';
import styles from './styleSheet';
import { switchIsLoaded } from './gamePage';
import statsImage from '../../assets/statsImage.png';

import { useDispatch } from 'react-redux';
import { incrementFirst } from '../../redux/firstTry';
import { incrementSecond } from '../../redux/secondTry';
import { incrementThird } from '../../redux/thirdTry';
import { incrementFourth } from '../../redux/fourthTry';
import { incrementFifth } from '../../redux/fifthTry';

export function WinPage({ navigation, route }) {
  const { wordArray, guessesUsed } = route.params;

  useEffect(() => {
    document.body.style.overflow = "hidden";
  }, []);

  function LetterOne(props) {
    return (
      <Animated.View
        style={[
          winStyles.letter1,
          {
            top: props.top,
          },
        ]}>
        <Text style={styles.winHeaderText}> {wordArray[0]} </Text>
      </Animated.View>
    );
  }

  function LetterTwo(props) {
    return (
      <Animated.View
        style={[
          winStyles.letter2,
          {
            top: props.top,
          },
        ]}>
        <Text style={styles.winHeaderText}> {wordArray[1]} </Text>
      </Animated.View>
    );
  }

  function LetterThree(props) {
    return (
      <Animated.View
        style={[
          winStyles.letter3,
          {
            top: props.top,
          },
        ]}>
        <Text style={styles.winHeaderText}> {wordArray[2]} </Text>
      </Animated.View>
    );
  }

  function LetterFour(props) {
    return (
      <Animated.View
        style={[
          winStyles.letter4,
          {
            top: props.top,
          },
        ]}>
        <Text style={styles.winHeaderText}> {wordArray[3]} </Text>
      </Animated.View>
    );
  }

  function LetterFive(props) {
    return (
      <Animated.View
        style={[
          winStyles.letter5,
          {
            top: props.top,
          },
        ]}>
        <Text style={styles.winHeaderText}> {wordArray[4]} </Text>
      </Animated.View>
    );
  }

  function Box(props) {
    return (
      <Animated.View
        style={[
          winStyles.cube,
          {
            top: props.top,
          },
        ]}>
        <Image style={styles.statsImage} source={statsImage} />
      </Animated.View>
    );
  }

  function BoxTwo(props) {
    return (
      <Animated.View
        style={[
          winStyles.cube,
          {
            top: props.topTwo,
          },
        ]}>
        <Image style={styles.statsImage} source={statsImage} />
      </Animated.View>
    );
  }

  const positionOne = useRef(new Animated.Value(25)).current;
  const positionTwo = useRef(new Animated.Value(25)).current;
  const positionThree = useRef(new Animated.Value(25)).current;
  const positionFour = useRef(new Animated.Value(25)).current;
  const positionFive = useRef(new Animated.Value(25)).current;

  const boxPositionOne = useRef(new Animated.Value(0)).current;

  const boxPositionTwo = useRef(
    new Animated.Value(0 - Dimensions.get('window').height)
  ).current;

  callAnimationOne();
  callAnimationTwo();

  function callAnimationOne() {
    Animated.timing(boxPositionOne, {
      easing: Easing.linear,
      toValue: Dimensions.get('window').height,
      duration: 10000,
      useNativeDriver: false,
    }).start(() => {
      Animated.timing(boxPositionOne, {
        toValue: 0,
        duration: 0,
        useNativeDriver: false,
      }).start(() => {
        callAnimationOne();
      });
    });
  }

  function callAnimationTwo() {
    Animated.timing(boxPositionTwo, {
      easing: Easing.linear,
      toValue: 0,
      duration: 10000,
      useNativeDriver: false,
    }).start(() => {
      Animated.timing(boxPositionTwo, {
        toValue: 0 - Dimensions.get('window').height,
        duration: 0,
        useNativeDriver: false,
      }).start(() => {
        callAnimationTwo();
      });
    });
  }

  function callAnimation() {
    Animated.timing(positionOne, {
      easing: Easing.linear,
      toValue: 0,
      duration: 100,
      useNativeDriver: false,
    }).start(() => {
      Animated.timing(positionTwo, {
        toValue: 0,
        duration: 100,
        useNativeDriver: false,
      }).start(() => {
        Animated.timing(positionThree, {
          toValue: 0,
          duration: 100,
          useNativeDriver: false,
        }).start(() => {
          Animated.timing(positionFour, {
            toValue: 0,
            duration: 100,
            useNativeDriver: false,
          }).start(() => {
            Animated.timing(positionFive, {
              toValue: 0,
              duration: 100,
              useNativeDriver: false,
            }).start(() => {
              Animated.timing(positionOne, {
                easing: Easing.linear,
                toValue: 25,
                duration: 100,
                useNativeDriver: false,
              }).start(() => {
                Animated.timing(positionTwo, {
                  toValue: 25,
                  duration: 25,
                  useNativeDriver: false,
                }).start(() => {
                  Animated.timing(positionThree, {
                    toValue: 25,
                    duration: 100,
                    useNativeDriver: false,
                  }).start(() => {
                    Animated.timing(positionFour, {
                      toValue: 25,
                      duration: 100,
                      useNativeDriver: false,
                    }).start(() => {
                      Animated.timing(positionFive, {
                        toValue: 25,
                        duration: 100,
                        useNativeDriver: false,
                      }).start(() => {
                        setTimeout(() => {
                          callAnimation();
                        }, 2000);
                      });
                    });
                  });
                });
              });
            });
          });
        });
      });
    });
  }

  setTimeout(() => {
    callAnimation();
  }, 2000);

  const dispatch = useDispatch();

  function updateStats() {
    switch (guessesUsed + 1) {
      case 1:
        dispatch(incrementFirst());
        break;
      case 2:
        dispatch(incrementSecond());
        break;
      case 3:
        dispatch(incrementThird());
        break;
      case 4:
        dispatch(incrementFourth());
        break;
      case 5:
        dispatch(incrementFifth());
        break;
      default:
    }
  }

  return (
    <SafeAreaView style={styles.screenContainer}>
    <View style={styles.winContainer}>
      <Box top={boxPositionOne} />
      <BoxTwo topTwo={boxPositionTwo} />

      <View style={styles.winCenterContainer}>
        <View style={winStyles.letterContainer}>
          <View style={winStyles.win1}>
            <View style={winStyles.l1}>
              <Text style={winStyles.textItem}>Y</Text>
            </View>
          </View>
          <View style={winStyles.win2}>
            <View style={winStyles.l2}>
              <Text style={winStyles.textItem}>O</Text>
            </View>
          </View>
          <View style={winStyles.win3}>
            <View style={winStyles.l3}>
              <Text style={winStyles.textItem}>U</Text>
            </View>
          </View>
          <View style={winStyles.win4}>
            <View style={winStyles.l4}>
              <Text style={winStyles.textItem}>W</Text>
            </View>
          </View>
          <View style={winStyles.win5}>
            <View style={winStyles.l5}>
              <Text style={winStyles.textItem}>I</Text>
            </View>
          </View>
          <View style={winStyles.win6}>
            <View style={winStyles.l6}>
              <Text style={winStyles.textItem}>N</Text>
            </View>
          </View>
        </View>

        <View style={winStyles.topContainer}>
          <Text style={{ fontSize: 16 }}>
            Guesses Used:{' '}
            <Text style={{ fontWeight: 'bold' }}>{guessesUsed + 1}</Text>
            <Text style={{ fontSize: 16 }}>{'\n'}</Text>
          </Text>
          <Text style={{ fontSize: 16 }}>The word was:</Text>
        </View>

        <View style={winStyles.midContainer}>
          <View style={styles.winFlexTwo}>
            <LetterOne top={positionOne} />
            <LetterTwo top={positionTwo} />
            <LetterThree top={positionThree} />
            <LetterFour top={positionFour} />
            <LetterFive top={positionFive} />
          </View>
        </View>

        <View style={winStyles.bottomContainer}>
          <View style={styles.winImageContainer}>
            <Image
              style={styles.winImage}
              source={require('../../assets/Mug_Animation.gif')}
            />
          </View>

          <TouchableHighlight
            underlayColor="green"
            style={winStyles.winButton}
            onPress={() => {
              updateStats();
              navigation.navigate('HomePage');
              switchIsLoaded();
            }}>
            <Text style={styles.winButtonText}> Home </Text>
          </TouchableHighlight>
        </View>
      </View>
    </View>
    </SafeAreaView>
  );
}

