import { createSlice } from '@reduxjs/toolkit';
import AsyncStorage from '@react-native-async-storage/async-storage';

const storeInPhoneStorage = async (score) => {
  await AsyncStorage.setItem("secondTry", score.toString());
}

export const counterSlice = createSlice({
  name: 'numSecondTry',
  initialState: {
    value: 0,
  },
  reducers: {
    incrementSecond: (state) => {
      state.value += 1;
      storeInPhoneStorage(state.value);
    },
    resetSecond: (state) => {
      state.value = 0;
      storeInPhoneStorage(state.value);
    },
    updateScoreTwo: (state, action) => {
      state.value = action.payload;
    }
  },
});

export const { incrementSecond, resetSecond, updateScoreTwo } = counterSlice.actions;
export default counterSlice.reducer;
