import {
  StyleSheet,
  Text,
  View,
  Image,
  SafeAreaView,
  TouchableHighlight,
} from 'react-native';
import homeButton from '../../assets/homeButton.png';
import bImage from '../../assets/b.png';
import uImage from '../../assets/u.png';
import eImage from '../../assets/e.png';
import rImage from '../../assets/r.png';
import tImage from '../../assets/t.png';

export function TutorialPage({ navigation }) {
  return (
    <SafeAreaView style={styles.screenContainer}>
      <View style={styles.container}>
        <View style={styles.tutorialContainer}>
          <View style={styles.rowEmpty}></View>
          <View style={styles.rowEmpty}>
            <View style={styles.rowColL}>
              <TouchableHighlight
                underlayColor="green"
                style={styles.statsHomeButton}
                onPress={() => {
                  navigation.push('HomePage');
                }}>
                <Image source={homeButton} style={styles.statsHomeButtonImage} />
              </TouchableHighlight>
            </View>
            <View style={styles.rowColR}></View>
          </View>
          <View style={styles.rowEmpty}>
            <Text style={{ fontWeight: 'bold', fontSize: 22 }}>How to play</Text>
          </View>
          <View style={styles.rowDescription}>
            <Text>
              Reveal the word!
              {'\n'}Submit a 5 letter word and use the hints to reveal letters.
            </Text>
          </View>
          <View style={styles.rowEmpty}>
            <Text style={{ fontWeight: 'bold', fontSize: 16, marginBottom: 10 }}>Examples</Text>
          </View>

          <View style={styles.row}>
            <View style={styles.rowColL}>
              <Image style={{width: 75, height: 75}} source={eImage} />
            </View>
            <View style={styles.rowColR}>
              <Text style={styles.tutorialText}>
                The letter in this spot comes before{' '}
                <Text style={{ fontWeight: 'bold', fontSize: 15 }}>E </Text>
                alphabetically.
              </Text>
              <Text style={styles.tutorialText}>
                {' '}
                {'\n'}
                Remaining letters:{' '}
                <Text style={{ fontWeight: 'bold', fontSize: 15 }}>
                  A, B, C, D{' '}
                </Text>
              </Text>
            </View>
          </View>
          <View style={styles.row}>
            <View style={styles.rowColL}>
              <Image style={{width: 75, height: 75}} source={rImage} />
            </View>
            <View style={styles.rowColR}>
              <Text style={styles.tutorialText}>
                The letter in this spot is correct
              </Text>
            </View>
          </View>
          <View style={styles.row}>
            <View style={styles.rowColL}>
              <Image style={{width: 75, height: 75}} source={tImage} />
            </View>
            <View style={styles.rowColR}>
              <Text style={styles.tutorialText}>
                The letter in this spot comes after{' '}
                <Text style={{ fontWeight: 'bold', fontSize: 15 }}>T </Text>
                alphabetically.
              </Text>
              <Text style={styles.tutorialText}>
                {' '}
                {'\n'}
                Remaining letters:{' '}
                <Text style={{ fontWeight: 'bold', fontSize: 15 }}>
                  U, V, W, X, Y, Z{' '}
                </Text>
              </Text>
            </View>
          </View>
          <View style={styles.row}>
            <View style={styles.rowColL}>
              <Image style={{width: 50, height: 50}} source={uImage} />
            </View>
            <View style={styles.rowColR}>
              <Text style={styles.tutorialText}>
                The letter can be in this position
              </Text>
            </View>
          </View>
          <View style={styles.row}>
            <View style={styles.rowColL}>
              <Image style={{width: 50, height: 50}} source={bImage} />
            </View>
            <View style={styles.rowColR}>
              <Text style={styles.tutorialText}>
                The letter cannot be in this position
              </Text>
            </View>
          </View>

        </View>
      </View>
    </SafeAreaView>
  );
}
const styles = StyleSheet.create({
  screenContainer: {
    backgroundColor: 'white',
    width: '100%',
    height: '100%',
    alignItems: 'center',
  },
  container: {
    backgroundColor: '#F8F8EC',
    width: 500,
    height: 1000,
    borderWidth: 1,
    borderColor: 'black',
  },
  tutorialContainer: {
    flex: 1,
    padding: 10,

  },
  row: {
    flex: 2,
    flexDirection: 'row',
    borderColor: 'grey',
    borderTopWidth: 0.25,
    borderBottomWidth: 0.25,
  },
  rowEmpty: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'flex-end',
  },
  rowDescription: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'flex-end',
    fontSize: 15,
  },
  rowColL: {
    flex: 1,
    justifyContent: 'center',
  },
  rowColR: {
    flex: 4,
    justifyContent: 'center',
  },
  statsHomeButton: {
    borderRadius: 30,
    width: 75,
    height: 75,
    backgroundColor: '#74C29C',
    shadowColor: '#000',
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 4,
    alignItems: 'center',
    justifyContent: 'center',
    borderColor: '#51876d',
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderBottomWidth: 5,
    shadowOffset: { width: -4, height: 10 },
  },
  statsHomeButtonImage: {
    width: 75,
    height: 75,
    justifyContent: 'center',
  },
  tutorialText: {
    fontSize: 15,
  },
});
