import { createSlice } from '@reduxjs/toolkit';
import AsyncStorage from '@react-native-async-storage/async-storage';

const storeInPhoneStorage = async (score) => {
  await AsyncStorage.setItem("fourthTry", score.toString());
}

export const counterSlice = createSlice({
  name: 'numFourthTry',
  initialState: {
    value: 0,
  },
  reducers: {
    incrementFourth: (state) => {
      state.value += 1;
      storeInPhoneStorage(state.value);
    },
    resetFourth: (state) => {
      state.value = 0;
      storeInPhoneStorage(state.value);
    },
    updateScoreFour: (state, action) => {
      state.value = action.payload;
    }
  },
});

export const { incrementFourth, resetFourth, updateScoreFour } = counterSlice.actions;
export default counterSlice.reducer;
