import { createSlice } from '@reduxjs/toolkit';
import AsyncStorage from '@react-native-async-storage/async-storage';

const storeInPhoneStorage = async (toggle) => {
  await AsyncStorage.setItem('musicEnabled', toggle.toString());
};

export const counterSlice = createSlice({
  name: 'enableMusic',
  initialState: {
    value: true,
  },
  reducers: {
    toggleMusicOn: (state) => {
      state.value = !state.value;
      storeInPhoneStorage(state.value);
    },
    updateMusic: (state, action) => {
      state.value = action.payload;
    }  
  },
});

export const { toggleMusicOn, updateMusic } = counterSlice.actions;
export default counterSlice.reducer;
