import { createSlice } from '@reduxjs/toolkit';
import AsyncStorage from '@react-native-async-storage/async-storage';

const storeInPhoneStorage = async (score) => {
  await AsyncStorage.setItem("fifthTry", score.toString());
}

export const counterSlice = createSlice({
  name: 'numFifthTry',
  initialState: {
    value: 0,
  },
  reducers: {
    incrementFifth: (state) => {
      state.value += 1;
      storeInPhoneStorage(state.value);
    },
    resetFifth: (state) => {
      state.value = 0;
      storeInPhoneStorage(state.value);
    },
    updateScoreFive: (state, action) => {
      state.value = action.payload;
    }
  },
});

export const { incrementFifth, resetFifth, updateScoreFive } = counterSlice.actions;
export default counterSlice.reducer;
