import { Provider } from 'react-redux';
import store from './redux/store';
import { HomePage } from './src/components/homePage';
import { StatsPage } from './src/components/statsPage';
import { WinPage } from './src/components/winPage';
import { LosePage } from './src/components/losePage';
import { GamePage } from './src/components/gamePage';
import { TutorialPage } from './src/components/tutorial';

import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
const Stack = createNativeStackNavigator();

export default function App() {
  return (
    <Provider store={store}>
      <NavigationContainer>
        <Stack.Navigator>
          <Stack.Screen
            name="HomePage"
            component={HomePage}
            options={{
              title: '',
              headerBackVisible: false,
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="StatsPage"
            component={StatsPage}
            options={{
              title: 'Your Stats',
              headerBackVisible: false,
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="GamePage"
            component={GamePage}
            options={{
              title: 'Word Around',
              headerBackVisible: false,
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="WinPage"
            component={WinPage}
            options={{
              title: 'Word Around',
              headerBackVisible: false,
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="LosePage"
            component={LosePage}
            options={{
              title: 'Word Around',
              headerBackVisible: false,
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="TutorialPage"
            component={TutorialPage}
            options={{
              title: 'How to play',
              headerBackVisible: false,
              headerShown: false,
            }}
          />
        </Stack.Navigator>
      </NavigationContainer>
    </Provider>
  );
}
