import { useRef, useEffect } from 'react';
import {
  View,
  Text,
  Image,
  Animated,
  StyleSheet,
  Dimensions,
  Easing,
  TouchableHighlight,
  Switch,
  SafeAreaView
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { useDispatch, useSelector } from 'react-redux';
import { resetFirst, updateScoreOne } from '../../redux/firstTry';
import { resetSecond, updateScoreTwo } from '../../redux/secondTry';
import { resetThird, updateScoreThree } from '../../redux/thirdTry';
import { resetFourth, updateScoreFour } from '../../redux/fourthTry';
import { resetFifth, updateScoreFive } from '../../redux/fifthTry';
import { toggleMusicOn, updateMusic } from '../../redux/musicEnabled';
import { toggleSfxOn, updateSfx } from '../../redux/sfxEnabled';

import styles from './styleSheet';
import statsImage from '../../assets/statsImage.png';
import homeButton from '../../assets/homeButton.png';
import colors from './colors.js';

function Box(props) {
  return (
    <Animated.View
      style={[
        statsStyles.cube,
        {
          top: props.top,
        },
      ]}>
      <Image style={styles.statsImage} source={statsImage} />
    </Animated.View>
  );
}

function BoxTwo(props) {
  return (
    <Animated.View
      style={[
        statsStyles.cube,
        {
          top: props.topTwo,
        },
      ]}>
      <Image style={styles.statsImage} source={statsImage} />
    </Animated.View>
  );
}

const getFromPhoneStorage = async (dispatch) => {
  const scoreOne = await AsyncStorage.getItem('firstTry');
  const scoreTwo = await AsyncStorage.getItem('secondTry');
  const scoreThree = await AsyncStorage.getItem('thirdTry');
  const scoreFour = await AsyncStorage.getItem('fourthTry');
  const scoreFive = await AsyncStorage.getItem('fifthTry');

  const sfx = await AsyncStorage.getItem('sfxEnabled');
  const music = await AsyncStorage.getItem('musicEnabled');

  dispatch(updateMusic(music ? music === 'true' : true));
  dispatch(updateSfx(sfx ? sfx === 'true' : true));

  dispatch(updateScoreOne(scoreOne ? parseInt(scoreOne) : 0));
  dispatch(updateScoreTwo(scoreTwo ? parseInt(scoreTwo) : 0));
  dispatch(updateScoreThree(scoreThree ? parseInt(scoreThree) : 0));
  dispatch(updateScoreFour(scoreFour ? parseInt(scoreFour) : 0));
  dispatch(updateScoreFive(scoreFive ? parseInt(scoreFive) : 0));
};

export const StatsPage = ({ navigation }) => {
  const position = useRef(new Animated.Value(0)).current;

  const positionTwo = useRef(
    new Animated.Value(0 - Dimensions.get('window').height)
  ).current;

  const firstValue = useSelector((state) => state.firstTry.value);
  const secondValue = useSelector((state) => state.secondTry.value);
  const thirdValue = useSelector((state) => state.thirdTry.value);
  const fourthValue = useSelector((state) => state.fourthTry.value);
  const fifthValue = useSelector((state) => state.fifthTry.value);
  const musicValue = useSelector((state) => state.musicEnabled.value);
  const sfxValue = useSelector((state) => state.sfxEnabled.value);

  const dispatch = useDispatch();

  useEffect(() => {
    callAnimation();
    callAnimationTwo();
    getFromPhoneStorage(dispatch);
    document.body.style.overflow = "hidden";
  }, []);

  function callAnimation() {
    Animated.timing(position, {
      easing: Easing.linear,
      toValue: Dimensions.get('window').height,
      duration: 10000,
      useNativeDriver: false,
    }).start(() => {
      Animated.timing(position, {
        toValue: 0,
        duration: 0,
        useNativeDriver: false,
      }).start(() => {
        callAnimation();
      });
    });
  }

  function callAnimationTwo() {
    Animated.timing(positionTwo, {
      easing: Easing.linear,
      toValue: 0,
      duration: 10000,
      useNativeDriver: false,
    }).start(() => {
      Animated.timing(positionTwo, {
        toValue: 0 - Dimensions.get('window').height,
        duration: 0,
        useNativeDriver: false,
      }).start(() => {
        callAnimationTwo();
      });
    });
  }

  const toggleMusic = () => dispatch(toggleMusicOn());
  const toggleSfx = () => dispatch(toggleSfxOn());

  return (
    <SafeAreaView style={statsStyles.screenContainer}>
    <View style={statsStyles.statsContainer}>
      <Box top={position} />
      <BoxTwo topTwo={positionTwo} />


      <TouchableHighlight
        underlayColor="green"
        style={statsStyles.statsHomeButton}
        onPress={() => {
          navigation.push('HomePage');
        }}>
        <Image source={homeButton} style={styles.statsHomeButtonImage} />
      </TouchableHighlight>

      <View style={statsStyles.statsButtonsContainer}>
        <View style={statsStyles.options}>
          <Text style={styles.statsHeaderText}>Options</Text>

          <View style={styles.statsMusicSwitch}>
            <Text style={statsStyles.optionsCL}>Music Enabled </Text>
            <View style={statsStyles.optionsCR}>
              <Switch
                trackColor={{ false: '#767577', true: '#81b0ff' }}
                thumbColor={musicValue ? '#f5dd4b' : '#f4f3f4'}
                ios_backgroundColor="#3e3e3e"
                onValueChange={toggleMusic}
                style={{ transform: [{ scaleX: 1.1 }, { scaleY: 1.1 }] }}
                value={musicValue}
              />
            </View>
          </View>

          <View style={styles.statsSfxSwitch}>
            <Text style={statsStyles.optionsCL}>Sfx Enabled </Text>
            <View style={statsStyles.optionsCR}>
              <Switch
                trackColor={{ false: '#767577', true: '#81b0ff' }}
                thumbColor={sfxValue ? '#f5dd4b' : '#f4f3f4'}
                ios_backgroundColor="#3e3e3e"
                onValueChange={toggleSfx}
                style={{ transform: [{ scaleX: 1.1 }, { scaleY: 1.1 }] }}
                value={sfxValue}
              />
            </View>
          </View>
        </View>
        <View style={statsStyles.stats}>
          <Text style={styles.statsHeaderText}>Stats</Text>

          <View
            style={[
              statsStyles.statsRows,
              { borderTopWidth: 0.25, borderTopColor: 'grey' },
            ]}>
            <View style={statsStyles.statsCL}>
              <Text style={styles.statsTryText}>Wins first try</Text>
            </View>
            <View style={statsStyles.statsCR}>
              <Text style={{ color: 'grey', fontWeight: 'bold' }}>
                {firstValue}
              </Text>
            </View>
          </View>

          <View style={statsStyles.statsRows}>
            <View style={statsStyles.statsCL}>
              <Text style={styles.statsTryText}>Wins second try</Text>
            </View>
            <View style={statsStyles.statsCR}>
              <Text style={{ color: 'grey', fontWeight: 'bold' }}>
                {secondValue}
              </Text>
            </View>
          </View>

          <View style={statsStyles.statsRows}>
            <View style={statsStyles.statsCL}>
              <Text style={styles.statsTryText}>Wins third try</Text>
            </View>
            <View style={statsStyles.statsCR}>
              <Text style={{ color: 'grey', fontWeight: 'bold' }}>
                {thirdValue}
              </Text>
            </View>
          </View>

          <View style={statsStyles.statsRows}>
            <View style={statsStyles.statsCL}>
              <Text style={styles.statsTryText}>Wins fourth try</Text>
            </View>
            <View style={statsStyles.statsCR}>
              <Text style={{ color: 'grey', fontWeight: 'bold' }}>
                {fourthValue}
              </Text>
            </View>
          </View>

          <View style={statsStyles.statsRows}>
            <View style={statsStyles.statsCL}>
              <Text style={styles.statsTryText}>Wins fifth try</Text>
            </View>
            <View style={statsStyles.statsCR}>
              <Text style={{ color: 'grey', fontWeight: 'bold' }}>
                {fifthValue}
              </Text>
            </View>
          </View>

          <View style={styles.statsButtonContainer}>
            <TouchableHighlight
              underlayColor="black"
              style={statsStyles.statsButtons}
              onPress={() => {
                dispatch(resetFirst());
                dispatch(resetSecond());
                dispatch(resetThird());
                dispatch(resetFourth());
                dispatch(resetFifth());
              }}>
              <Text style={styles.homeButtonText}> Reset Stats </Text>
            </TouchableHighlight>
          </View>
        </View>
      </View>
    </View>
    </SafeAreaView>
  );
};

const statsStyles = StyleSheet.create({
  screenContainer: {
    backgroundColor: 'white',
    width: '100%',
    height: '100%',
    alignItems: 'center',
  },
  statsContainer: {
    backgroundColor: '#FDF2D6',
    width: 500,
    height: 1000,
    borderRightWidth: 1,
    borderLeftWidth: 1,
    borderColor: 'black',
    alignItems: 'center',
    justifyContent: 'center',
  },
  statsButtonsContainer: {
    width: '70%',
    height: '70%',
    padding: 20,
    backgroundColor: 'white',
    shadowColor: '#000',
    borderRadius: 30,
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 7,
    alignItems: 'center',
    borderColor: 'grey',
    borderTopWidth: 0.5,
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderBottomWidth: 2,
    shadowOffset: { width: -4, height: 10 },
  },
  cube: {
    position: 'absolute',
  },
  options: {
    flex: 1,
    width: '95%',
    justifyContent: 'center',
  },
  optionsCL: {
    flexDirection: 'row',
    flex: 1,
    textAlign: 'left',
    paddingLeft: 5,
  },
  optionsCR: {
    flexDirection: 'row',
    flex: 1,
    justifyContent: 'flex-end',
    paddingRight: 5,
  },
  stats: {
    flex: 2,
    width: '95%',
    borderTopWidth: 1,
    borderColor: 'black',
  },
  statsRows: {
    flexDirection: 'row',
    flex: 1,
    borderBottomWidth: 0.25,
    borderBottomColor: 'grey',
  },
  statsCL: {
    flexDirection: 'row',
    flex: 2,
    textAlign: 'left',
    paddingLeft: 5,
    alignItems: 'center',
  },
  statsCR: {
    flexDirection: 'row',
    flex: 1,
    justifyContent: 'flex-end',
    paddingRight: 5,
    alignItems: 'center',
  },
  statsButtons: {
    width: '100%',
    height: 50,
    borderRadius: 45,
    backgroundColor: colors.button,
    alignItems: 'center',
    justifyContent: 'center',
    shadowColor: '#000',
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 4,
    borderColor: '#94866a',
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderBottomWidth: 5,
    shadowOffset: { width: -4, height: 10 },
  },
  statsHomeButton: {
    position: 'absolute',
    borderRadius: 30,
    top: 30,
    left: 20,
    width: 75,
    height: 75,
    backgroundColor: '#74C29C',
    shadowColor: '#000',
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 4,
    alignItems: 'center',
    justifyContent: 'center',
    borderColor: '#51876d',
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderBottomWidth: 5,
    shadowOffset: { width: -4, height: 10 },
  },
 
});
