import { createSlice } from '@reduxjs/toolkit';
import AsyncStorage from '@react-native-async-storage/async-storage';

const storeInPhoneStorage = async (score) => {
  await AsyncStorage.setItem("thirdTry", score.toString());
}

export const counterSlice = createSlice({
  name: 'numThirdTry',
  initialState: {
    value: 0,
  },
  reducers: {
    incrementThird: (state) => {
      state.value += 1;
      storeInPhoneStorage(state.value);
    },
    resetThird: (state) => {
      state.value = 0;
      storeInPhoneStorage(state.value);
    },
    updateScoreThree: (state, action) => {
      state.value = action.payload;
    }
  },
});

export const { incrementThird, resetThird, updateScoreThree } = counterSlice.actions;
export default counterSlice.reducer;
