import { createSlice } from '@reduxjs/toolkit';
import AsyncStorage from '@react-native-async-storage/async-storage';

const storeInPhoneStorage = async (score) => {
  await AsyncStorage.setItem("firstTry", score.toString());
}

export const counterSlice = createSlice({
  name: 'numFirstTry',
  initialState: {
    value: 0,
  },
  reducers: {
    incrementFirst: (state) => {
      state.value += 1;
      storeInPhoneStorage(state.value);
    },
    resetFirst: (state) => {
      state.value = 0;
      storeInPhoneStorage(state.value);
    },
    updateScoreOne: (state, action) => {
      state.value = action.payload;
    }
  },
});

export const { incrementFirst, resetFirst, updateScoreOne } = counterSlice.actions;
export default counterSlice.reducer;
