import {configureStore} from "@reduxjs/toolkit";
import firstTry from './firstTry'
import secondTry from './secondTry'
import thirdTry from './thirdTry'
import fourthTry from './fourthTry'
import fifthTry from './fifthTry'
import musicEnabled from './musicEnabled'
import sfxEnabled from './sfxEnabled'

export default configureStore({
  reducer: {
    firstTry: firstTry,
    secondTry: secondTry,
    thirdTry: thirdTry,
    fourthTry: fourthTry,
    fifthTry: fifthTry,
    musicEnabled: musicEnabled,
    sfxEnabled: sfxEnabled,
  }
});