import { useState, useEffect, useRef } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Animated,
  TouchableHighlight,
  TouchableOpacity,
  SafeAreaView,
} from 'react-native';
import { Audio } from 'expo-av';
import { useSelector } from 'react-redux';
import colors from '../../src/components/colors.js';
import { fullKeyboard, CLEAR, keyWidth } from '../../src/components/keyboard';
import styles from '../../src/components/styleSheet';
import wordList from '../../src/components/listOfWords';
import { _TIMERDELAY } from '../../src/components/constants';

const gamesong = new Audio.Sound();
var isMusicLoaded = false;
var isLoaded;
var lettersFound;
var keysGreyOne;
var keysGreyTwo;
var keysGreyThree;
var keysGreyFour;
var keysGreyFive;
var wordArray;

var totalLettersFound = 0;

function fetchWord() {
  const randomWord = wordList[Math.floor(Math.random() * wordList.length)];
  const randomToUpper = randomWord.toUpperCase();
  wordArray = randomToUpper.split('');
  console.log(randomWord);
}

export function switchIsLoaded() {
  if (isLoaded) {
    isLoaded = false;
  }
}

